body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ui {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(255, 255, 255, .5);
  padding: 1em;
  line-height: 1.25em;
  max-width: 15vw
}

.ui p {
  font-weight: bold;
}

.ui label {
  display: block;
}

.ui div {
  padding-bottom: 1em;
}

body {
    background: #000;
    color: #fff;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    overflow: hidden;
}

canvas {
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    height: auto !important;
}

.dots {
    width: 100%;
}

.txt {
    color: #fff;
}

.grid {
    display: -webkit-flex;
    display: flex;
}

.controls {
    padding: 1em;
}

input.small {
    max-width: 50px;
}

.row {
    display: -webkit-flex;
    display: flex;
}

